.image-paggination-container
    z-index: 11
    top: 0
    left: 0
    width: 100%
    height: 100vh
    position: fixed
    background-color: #000000ed
    .image-paggination-download
        position: fixed
        bottom: 0
        right: 50%
        background: #043053
        padding: 0.5rem 0.8rem
        border-radius: 1.5rem
        cursor: pointer
        margin-bottom: 1rem 
        transform: translate(50%,0)
        display: flex
        align-items: center
        gap: .6rem
        transition: all .2s
        &:hover
            background: #09253c
            
    .image-paggination-close
        color: #fff
        position: fixed
        width: 2rem
        height: 2rem
        padding: 0.4rem
        margin: 1rem
        cursor: pointer 
        top: 0
        right: 0
        border-radius: 50%
        transition: all 0.2s
        &:hover
            background-color: #8e8e8eb5
            
    .image-paggination-block
        position: relative
        width: 100%
        height: 100vh
        display: flex
        align-items: center
        justify-content: center
        .paggination-move
            width: 2rem
            height: 2rem
            padding: .4rem
            margin: .4rem
            cursor: pointer
            border-radius: 50%
            transition: all 0.2s
            display: flex
            align-items: center
            justify-content: center
            &:hover
                background-color: #8e8e8eb5
        .image-paggination-box    
            position: relative
            width: 100%
            height: 100vh
            display: flex
            align-items: center
            justify-content: center
            max-width: 90%
            max-height: 80vh
            .image-paggination-image-item
                width: 100%
                height: 100%
                object-fit: contain



