.fast-page-background
    z-index: 20
    position: absolute
    width: 100%
    height: 100vh
    top: 0
    left: 0
    background-color: #2c2c2c8a
    display: flex
    align-items: center
    justify-content: center
    .fast-page-container
        width: 80%
        max-width: 30rem 
        min-height: 10rem 
        max-height: 20rem 
        background-color: var(--background-color)
        padding: 1rem
        border-radius: 1rem
        .fast-page-header
            width: 100%
            position: relative
            padding-bottom: .87rem 
            margin-bottom: 1rem 
            border-bottom: 1px solid #333
            .fast-page-close
                position: absolute
                top: 0
                right: 0
                cursor: pointer
                width: 1.7rem
                height: 1.7rem
                padding: .3rem
                border-radius: 50%
                transition: all .2s
                &:hover
                    background: #353535
        .fast-page-body
            width: 100%
            height: 100%
    
.fastpage-body-box
    width: 100%
    height: 100%
    display: flex
    flex-direction: column
    .text-input
        width: 100%
        height: 1rem
        padding: 1rem 1rem
        background: transparent
        border: 1px solid #333
        color: var(--text-color)
        border-radius: .3rem
    .buttons
        width: 100%
        padding: 1rem 0
        display: flex
        .button
            padding: .6rem 1rem
            border-radius: .4rem
            border: 1px solid #333
            background: var(--background-color)
            color: var(--text-color)
            margin-left: auto 
            cursor: pointer
            transition: all .2s
            &:hover
                background: var(--background-color-1)
                
            &:disabled
                cursor: not-allowed
                &:hover
                    background: var(--background-color)
                

.delete-chat-fast-page-text
    text-align: center
    margin-top: 2rem 
.update-chat-fast-page-input-text
    width: 100%
    padding: .4rem .6rem
    background-color: transparent
    border: 1px solid #333
    border-radius: .3rem
    color: var(--text-color)

.delete-chat-fast-page-buttons, .update-chat-fast-page-buttons, .delete-galary-any-confirm
    width: 100%
    display: flex
    justify-content: flex-end
    margin-top: 1rem 
    gap: .8rem
    button
        padding: .6rem .8rem
        border-radius: .5rem
        cursor: pointer
        background-color: var(--background-color)
        color: var(--text-color)
        border: 1px solid #333
        &:hover
            background-color: var(--background-color-1)
        &.delete
            background-color: #be1111
            &:hover
                background-color: #ed1717
        &.update
            background-color: #1abe11
            &:hover
                background-color: #128e09

.login-container
    display: flex
    align-items: center
    justify-content: center
    min-height: 100vh
    width: 100%
    padding: 4rem
    background-color: var(--background-color)
    .login-box
        padding: 1.2rem
        width: 24rem
        /* background-color: var(--background-color-2) */
        display: flex
        align-items: center
        justify-content: center
        flex-direction: column

        .login-input
            display: flex
            font-size: .875rem
            flex-direction: column
            width: 100%
            margin-bottom: 1rem
            label
                margin-bottom: 1rem
            input
                background-color: transparent
                padding: 0.3rem 1rem
                border: 1px solid #333
                border-radius: 0.3rem
                color: var(--text-color)
                outline: none
                line-height: calc(1.25 / .875)

            input:active
                outline: none
        .btn
            width: 100%
            text-align: center
            cursor: pointer
            border: 1px solid #333
            padding: .5rem 8rem
            border-radius: 0.3rem
            &:hover
                background-color: var(--background-color-1)